/* Modals */
esign.modals = function () {
  esign.cache.dataSelected = false;

  // btns
  $('.modal-button, .main-modal-trigger').click(function(e) {
    e.preventDefault();
    var $modal;

    if($(this).hasClass('main-modal-trigger')) {
      $modal = $('#main-modal');
    } else {
      $modal = $($(this).attr('href'));
    }

    /* convert to md-cover if height > window height or device is mobile */
    if($modal.height() > $(window).height() || esign.cache.isMobile) {

      if(!$modal.hasClass('md-cover')) {

        var $close = $modal.find('.md-close');

        $modal
          .addClass('md-cover')
          .find('.md-content')
          .wrapInner('<div class="md-center"><div class="container small"></div></div>')
          .after($close);

        $close.wrap('<div class="md-topbar"></div>');
      }
    }

    if($modal) {
      // remove current modal
      $('.md-show').removeClass('md-show');
      esign.cache.$html.removeClass('noscroll');

      // show modal
      $modal.toggleClass('md-show');
      if($modal.hasClass('md-cover')) {
        esign.cache.$html.addClass('noscroll');
      }

      location.hash = '#open-' + $modal.attr('id');
    }
  });

  // open modal from hash
  var hash = window.location.hash;
  if(hash.indexOf('#open-') >= 0) {
    var itemId = hash.replace('open-',''),
      $modal = $(itemId);

    if($modal.length && esign.cache.dataSelected) {
      $modal.addClass('md-show');
      if($modal.hasClass('md-cover')) {
        esign.cache.$html.addClass('noscroll');
      }
    } else {
      location.hash = '';
      esign.resetState();
    }
  }

  // close modal
  $('.md-close-trigger, .md-overlay').click(function(e) {
    esign.closeModal();
  });

  // handle keyboard events
  $(document).on('keydown', function(e) {
    var tag = e.target.tagName.toLowerCase();

    if (tag !== 'input' && tag !== 'textarea') {
      // hide modal on escape
      if(e.which === 27 && $('.md-show').length) {
        esign.closeModal();
      }

      if(e.which === 77) {
        $('.main-modal-trigger').trigger('click');
      }
    }
  });

  var $triggerFirstGraph = $('#first-ndc-graph');
  var $triggerUpdatedGraph = $('#updated-ndc-graph');

  if ($triggerFirstGraph.length) {
    $triggerFirstGraph.click(function (e) {
      e.preventDefault();

      var isoSelected = esign.cache.isoSelected;

      if (esign.cache.warmingMode !== false && !$triggerFirstGraph.hasClass('active')) {
        $triggerUpdatedGraph.removeClass('active');
        $triggerFirstGraph.addClass('active');
        esign.closeModal();
        $('#first-ndc').click();

        window.setTimeout(function() {
          if (isoSelected === 'EU27') {
            $('[data-iso="EU28"]').click();
          } else {
            $('[data-iso="' + isoSelected + '"]').click();
          }
        }, 300);
      }
    });
  }

  if ($triggerUpdatedGraph.length) {
    $triggerUpdatedGraph.click(function (e) {
      e.preventDefault();

      var isoSelected = esign.cache.isoSelected;

      if (esign.cache.warmingMode !== true && !$triggerUpdatedGraph.hasClass('active')) {
        $triggerFirstGraph.removeClass('active');
        $triggerUpdatedGraph.addClass('active');
        esign.closeModal();
        $('#updated-ndc').click();

        window.setTimeout(function() {
          if (isoSelected === 'EU28') {
            $('[data-iso="EU27"]').click();
          } else {
            $('[data-iso="' + isoSelected + '"]').click();
          }
        }, 300);
      }
    });
  }
};

esign.closeModal = function () {
  $('.md-show').removeClass('md-show');
  esign.cache.$html.removeClass('noscroll');
  location.hash = '';
  esign.cache.modalDataView = false;
  esign.resetState();
};

/* Country list */
esign.countryList = function () {
  if($('#country-list').length) {
    if (esign.cache.countries !== null) {
      initCountryList();
    } else {
      $.getJSON('assets/data/countries.json?v=' + esign.cache.version, function(data) {
        esign.cache.countries = data;
        initCountryList();
      });
    }
  }

  function initCountryList() {
    for(var i = 0; i < esign.cache.countries.length; i++ ) {
      // TODO remove earth from countries.json?
      if(esign.cache.countries[i]['iso-a3'] !== 'EARTH') {
        // Don't lowercase country groupes
        if(!esign.cache.countryGroupes.includes(esign.cache.countries[i]['iso-a3'])) {
          esign.cache.countries[i]['country'] = esign.cache.countries[i]['country'].toLowerCase().replace(/(^|\s)[a-z]/g, function (f) {
            return f.toUpperCase();
          });
        }
        $('#country-list ul').append('<li data-iso="' + esign.cache.countries[i]['iso-a3'] + '" data-country="' + esign.cache.countries[i]['country'] + '"><p class="country">' + esign.cache.countries[i]['country'] + '</p> <span class="vh iso">' + esign.cache.countries[i]['iso-a3'] + '</span></li>');
      }
      if(esign.cache.countries[i]['country'].includes(',')) esign.cache.countries[i]['country'] = esign.cache.countries[i]['country'].split(',')[1].trim() + ' ' + esign.cache.countries[i]['country'].split(',')[0];
    }

    var list = new List('country-list', {
      valueNames: ['country', 'iso'],
      sort:['country'],
    });

    // Order list so country groupes are placed first
    list.sort('country', {
      asc: true,
      sortFunction: function(itemA, itemB) {
        var sort = list.utils.naturalSort;
        var sortKeyA = esign.cache.countryGroupes.includes(itemA._values.iso) ? 'A ' + itemA._values.country : itemA._values.country;
        var sortKeyB = esign.cache.countryGroupes.includes(itemB._values.iso) ? 'A ' + itemB._values.country : itemB._values.country;
        return sort(sortKeyA, sortKeyB);
      }
    });
  }
};

