/* TODO: remove: Fill fairness statement */
esign.fairnessStatement = function(iso) {
  if (esign.cache.warmingModeUpdated) {
    var fieldsUpdated = ['status', 'date', '6a', '6b', '6c', '6d', '6e', '7a', '7b', 'conditionality', 'comments'];
    var fieldsUpdatedEU27 = ['latest_available', 'source', 'note'];

    $.getJSON('assets/data/fairness-all-updated.json?v=' + esign.cache.version, function(data) {
      try {
        function getCountryIso(value) {
          return value['iso-a3'] === iso;
        }

        var fairnessStatement = data.filter(getCountryIso)[0];


        if (esign.cache.countriesEU27.indexOf(fairnessStatement['iso-a3']) > -1) {
          $('.fairness-statement-updated').addClass('hide');
          $('.fairness-statement-updated-eu27').removeClass('hide');

          for (var i = 0; i < fieldsUpdatedEU27.length; i += 1) {
            $('.description-' + fieldsUpdatedEU27[i]).html(
              fairnessStatement[fieldsUpdatedEU27[i]] && fairnessStatement[fieldsUpdatedEU27[i]].length
                ? fairnessStatement[fieldsUpdatedEU27[i]] : 'Not Specified'
            );
          }
        } else {
          $('.fairness-statement-updated').removeClass('hide');
          $('.fairness-statement-updated-eu27').addClass('hide');

          for (var i = 0; i < fieldsUpdated.length; i += 1) {
            $('.description-' + fieldsUpdated[i]).html(
              fairnessStatement[fieldsUpdated[i]] && fairnessStatement[fieldsUpdated[i]].length
                ? fairnessStatement[fieldsUpdated[i]] : 'Not Specified'
            );
          }
        }

        $('.fairness-statement-credits').addClass('hide');
      } catch(e) {
        $('.fairness-statement-updated').removeClass('hide');
        $('.fairness-statement-updated-eu27').addClass('hide');

        for (var i = 0; i < fieldsUpdatedEU27.length; i += 1) {
          $('.description-' + fieldsUpdatedEU27[i]).html('Not Specified');
        }
        for (var i = 0; i < fieldsUpdated.length; i += 1) {
          $('.description-' + fieldsUpdated[i]).html('Not Specified');
        }

        $('.fairness-statement-credits').addClass('hide');
      }
    });
  } else {
    $.getJSON('assets/data/fairness-all.json?v=' + esign.cache.version, function(data) {
      try {
        function getCountryIso(value) {
          return value['iso-a3'] === iso;
        }
        var fairnessStatement = data.filter(getCountryIso);
        $('.description-f').html(fairnessStatement[0]['Description of fairness']);
        $('.description-a').html(fairnessStatement[0]['Description of ambition']);
        $('.description-c').html(fairnessStatement[0]['Description of how it contributes towards achieving the objective of the Convention']);
        $('.description-indc').html(fairnessStatement[0]['Conditionality of the NDC']);
        $('.description-updated').html(fairnessStatement[0]['Last updated (in the Module)']);
        $('.fairness-statement-credits').removeClass('hide');
      } catch(e) {
        $('.description-f').html('Not Specified');
        $('.description-a').html('Not Specified');
        $('.description-c').html('Not Specified');
        $('.description-indc').html('Not Specified');
        $('.description-updated').html('Not Specified');
        $('.fairness-statement-credits').removeClass('hide');
      }
    });
  }
};

/* Qtip */
esign.qtip = function () {
  if(!esign.cache.isMobile) {
    addStarTooltip($('.tooltip--cer'), 'Constant Emissions Ratio', 'Maintains current national emissions ratios. All countries mitigate at the same rate.');
    addStarTooltip($('.tooltip--gdr'), 'Greenhouse Development Rights', 'Rich countries with high historical per capita emissions should reduce emissions the most, other countries\' emissions can temporarily increase.');
    addStarTooltip($('.tooltip--cap'), 'Capability', 'Rich countries should reduce emissions the most, other countries\' emissions can temporarily increase.');
    addStarTooltip($('.tooltip--cpc'), 'Equal Cumulative Per Capita', 'Countries with high historical per capita emissions should reduce emissions more the future.');
    addStarTooltip($('.tooltip--epc'), 'Equal Per Capita', 'All countries converge to equal per capita emissions.');
    $('.indc-tip').qtip({
      content: {
        text: "NDCs  Nationally Determined Contributions - are climate action plans that were submitted to the UNFCCC during the lead-up to the Paris Agreement in 2015."
      },
      position: {
        my: 'bottom center',
        at: 'top center'
      }
    });
    $('.grading-tip').qtip({
      content: {
        text: "The number of stars given to each country corresponds to the number of visions of equity met by its Nationally Determined Contribution. A country with a higher number of stars can be considered ambitious according to more visions of equity, and is therefore likely to be considered ambitious by a greater number of countries. However, it would not appear ambitious according to the visions of equity that are not matched, and as such could not be considered as universally ambitious."
      },
      position: {
        my: 'bottom left',
        at: 'top center'
      }
    });
  }

  function addStarTooltip(selector, title, text) {
    selector.qtip({
      content: {
        title: title,
        text: text
      },
      position: {
        my: 'bottom center',
        at: 'top center'
      }
    });
  }
};

/* Reset map, charts & elements */
esign.resetState = function() {
  esign.cache.isoSelected = '';
  esign.cache.countrySelected = '';
  esign.cache.isAbsolute = true;

  esign.cache.$relativeTrigger
    .addClass('active')
    .html('Relative to 2010 <span class="caret"></span><span class="sr-only">Toggle Dropdown</span>');
  esign.cache.$relativeTrigger2
    .addClass('active')
    .html('Relative to 2010 <span class="caret"></span><span class="sr-only">Toggle Dropdown</span>');
  esign.cache.$relativeTrigger3
    .removeClass('active')
    .html('Relative to 2021 <span class="caret"></span><span class="sr-only">Toggle Dropdown</span>');
  esign.cache.$absoluteTrigger.removeClass('active');
  esign.cache.$absoluteTrigger2.removeClass('active');
  // esign.cache.$absoluteTrigger3.removeClass('active');
  esign.cache.$absoluteTrigger3.addClass('active');
  $('#chart').empty();
  $('#chart2').empty();
  $('#chart3').empty();
  $('.how-to-read').css('display', 'none');

  esign.cache.$absoluteTrigger.unbind( "click" );
  esign.cache.$absoluteTrigger2.unbind( "click" );
  esign.cache.$absoluteTrigger3.unbind( "click" );
  $('.rel-year').unbind( "click" );
  $('.read-button').unbind( "click" );
  $('#one-graph').unbind( "click" );
  $('#two-graph').unbind( "click" );
  $('#addPlegde').unbind( "click" );
  $('#custom-pledge-close').unbind( "click" );
  $('#updatePledge').unbind( "click" );
  $('#openRead').unbind( "click" );
  $('#openRead2').unbind( "click" );
  $('#how-to-read-close').unbind( "click" );
  $('#how-to-read-close2').unbind( "click" );

  setTrafficLightCond(false);
  setTrafficLightUncond(false);

  esign.resetExportListeners();
};

