/* Load warming map data */
esign.initWarmingMapData = function() {
  esign.cache.filenameMap = 'Global emissions [Downloaded from trafficlightclimate.org]';

  // Capitalize first letter to match naming of cached data
  const approachMode = esign.cache.approachMode;
  const approachModeCapital = approachMode.charAt(0).toUpperCase() + approachMode.slice(1);

  const dataName = `warming${approachModeCapital}${esign.cache.responsibilitySince}`;
  const dataNameEU = `warming${approachModeCapital}${esign.cache.responsibilitySince}_EU`;

  esign.replaceEUValues(dataName, dataNameEU);
  esign.drawWarmingMap(esign.cache[dataName], esign.cache[dataNameEU], 'EU27');
};

/* Draw map, data of each single country must be in the 1.2-5.1 range to maintain correct color coding */
esign.drawWarmingMap = function(data, dataEU, seriesName) {
  // Allow nullInteraction fix
  Highcharts.seriesTypes.map.prototype.pointClass.prototype.onMouseOver = function (e) {
    clearTimeout(this.colorInterval);
    if (this.value !== null || this.series.options.nullInteraction) {
      Highcharts.Point.prototype.onMouseOver.call(this, e);
    } else {
      this.series.onMouseOut(e);
    }
  }

  var seriesOptions = function (seriesData, seriesDataEU, seriesName) {
    return {
      name: seriesName,
      data: seriesDataEU,
      mapData: Highcharts.geojson(Highcharts.maps['custom/world-eckert3-highres']),
      backgroundColor: '#FCFFC5',
      joinBy: 'iso-a3',
      cursor: 'pointer',
      nullInteraction: esign.cache.warmingGdp1990,
      states: {
        hover: {
          borderColor: 'grey',
          color: this.color
        }
      },
      dataLabels: {
        enabled: false,
        useHTML: true
      },
      point: {
        events:{
          click: function (event) {
            esign.cache.dataSelected = true;
            esign.cache.isoSelected = this['iso-a3'];

            for (var i = 0; i < esign.cache.countries.length; i++ ) {
              if(esign.cache.countries[i]['iso-a3'] === esign.cache.isoSelected) esign.cache.countrySelected = esign.cache.countries[i]['country'];
            }
            if (event.point) {
              console.log('if event.point does exist => map click');
              if (event.point.options && !event.point.options.labelrank) {
                esign.openWarmingChart(this.color, findConditionalValue(this['iso-a3']), findUnconditionalValue(this['iso-a3']));
              }
            } else {
              console.log('if event.point does not exist => list click');
              esign.openWarmingChart(this.color, findConditionalValue(this['iso-a3']), findUnconditionalValue(this['iso-a3']));
            }
          },
          mouseOver: function (event) {
            setTrafficLightCond(true, findConditionalValue(this['iso-a3']));
            setTrafficLightUncond(true, findUnconditionalValue(this['iso-a3']));

            // todo check what this does?
            if (event.target && event.target.options && !event.target.options.labelrank) {
              event.target.graphic.element.classList.remove('highcharts-null-point')
            }
          },
          mouseOut: function () {
            // reset traffic light
            if(!esign.cache.modalDataView) {
              setTrafficLightCond(false);
              setTrafficLightUncond(false);
            }

          }
        }
      },
      nullColor: '#F1F1F1',
      borderColor: '#D2D2D2',
      borderWidth: 1
    };
  }

  esign.cache.map = new Highcharts.Map('map2', {
    title: {
      text: esign.cache.filenameMap,
      style: {
        fontSize: '8'
      },
      useHTML: true
    },
    exporting: {
      enabled: false,
      filename: esign.cache.filenameMap,
      fallbackToExportServer: true,
      chartOptions: {
        credits: {
          enabled: true,
          href:"#",
          position: {
            align:"right",
            verticalAlign:"bottom",
            x: -10,
            y: -5
          },
          style: {"cursor": "default", "font-size":"6pt"},
          text:"From trafficlightclimate.org, Commitioned by the CVF/V20"
        },
        chart:{
          events: {
            load: function(event) {
              this.renderer.image('assets/images/legend-warming.png',10,260,100,129).add();
            }
          }
        }
      }
    },
    chart: {
      margin: [0, 0, 0, 0],
      style: {
        fontFamily: 'Open Sans'
      },
      backgroundColor: '#f6faff',
      pinchType: 'none',
    },
    credits: false,
    mapNavigation: {
      enabled: true,
      enableMouseWheelZoom: false,
      enableDoubleClickZoom: false,
      enableDoubleClickZoomTo: false,
      buttonOptions: {
        align: 'right',
        verticalAlign: 'bottom',
        x: -20,
        theme: {
          fill: 'white',
          'stroke-width': 1,
          stroke: 'silver',
          r: 0,
          states: {
            hover: {
              fill: '#f5f5f5'
            }
          }
        }
      },
      buttons: {
        zoomIn: {
          y: -20
        },
        zoomOut: {
          y: 8
        }
      }
    },
    colorAxis: {
      stops: [
        [0.2, '#91C33F'],
        [0.5, '#F5B927'],
        [0.8, '#D43D47'],
      ],
    },
    legend: {
      enabled: false,
      align: 'center',
      x: 100,
      y: 100,
      color: '#fff',
      layout: 'horizontal',
      floating: false,
      verticalAlign: 'bottom',
      shadow: false,
      border: 0,
      borderRadius: 0,
      borderWidth: 0
    },
    tooltip: {
      enabled: !esign.cache.isMobile,
      useHTML: true,
      borderWidth:0,
      borderRadius:0,
      padding: 5,
      minWidth: 300,
      backgroundColor: false,
      shadow: false,
      hideDelay: 30,
      formatter: function () {
        var iso = this.point['iso-a3'],
          name;

        for (var i = 0; i < esign.cache.countries.length; i++) {
          if(esign.cache.countries[i]['iso-a3'] === iso) {
            name = esign.cache.countries[i]['country'];
            break;
          }
        }

        if (name === undefined) {
          return false;
        }

        if(esign.cache.countriesEU27.indexOf(iso) > -1) {
          return `<div class="tool">
            <h4>European Union</h4>
            <div class="tool__value"> <span class="colour colour-${signalDisplayValue(findConditionalValue('EU'))}"></span>${signalDisplayNumber(findConditionalValue('EU'))} (cond)</div>
            <div class="tool__value"> <span class="colour colour-${signalDisplayValue(findUnconditionalValue('EU'))} "></span>${signalDisplayNumber(findUnconditionalValue('EU'))} (uncond)</div>
            <hr>
            <h4>${name}</h4>
            <div class="tool__value"> <span class="colour colour-${signalDisplayValue(findConditionalValue(iso))}"></span>${signalDisplayNumber(findConditionalValue(iso))} (cond)</div>
            <div class="tool__value"> <span class="colour colour-${signalDisplayValue(findUnconditionalValue(iso))} "></span>${signalDisplayNumber(findUnconditionalValue(iso))} (uncond)</div>
          </div>`;
        } else {
          // TODO work out exeptions? when data is unknown?
            return `<div class="tool">
            <h4>${name}</h4>
            <div class="tool__value"> <span class="colour colour-${signalDisplayValue(findConditionalValue(iso))}"></span>${signalDisplayNumber(findConditionalValue(iso))} (cond)</div>
            <div class="tool__value"> <span class="colour colour-${signalDisplayValue(findUnconditionalValue(iso))} "></span>${signalDisplayNumber(findUnconditionalValue(iso))} (uncond)</div>
          </div>`;
        }
      }
    },
    series: [seriesOptions(data, dataEU, seriesName)]
  });

  // TODO check if still is needed, or replaced by %
  function temperatureDisplayValue(value) {
    var ndc;
    var offLimit = '';

    value != null ? ndc = value : ndc = 'N/A';

    if (ndc === 5.1 || ndc > 5.1) {
      offLimit = '&ge; ';
      ndc = 5.1;
    } else if (ndc === 1.2 || ndc < 1.2) {
      offLimit = '&le; ';
      ndc = 1.2;
    }

    if (ndc === 'N/A') {
      return ndc;
    }

    return offLimit + ndc + 'C';
  }

  function signalDisplayValue(value) {
    var color;

    if (value === 5) {
      color = 'Red';
    } else if (value === 3) {
      color = 'Orange';
    }  else if (value === 1) {
      color = 'Green';
    }  else if (value === 0) {
      color = 'N/A';
    }
    return color;
  }

  // TODO rename
  function signalDisplayNumber(value) {
    var color;

    if (value === 5) {
      color = '>2C';
    } else if (value === 3) {
      color = '<2C';
    }  else if (value === 1) {
      color = '~1.5C';
    }  else if (value === 0) {
      color = 'N/A';
    }
    return color;
  }

  // Tod rework both functions as one?
  function findConditionalValue(iso) {
    var value;
    // var data;

    // Capitalize first letter to match naming of cached data
    const approachMode = esign.cache.approachMode;
    const approachModeCapital = approachMode.charAt(0).toUpperCase() + approachMode.slice(1);

    const dataName = `warming${approachModeCapital}${esign.cache.responsibilitySince}`;
    const data = esign.cache[dataName];

    if(data.length != 0) {
      for (var i = 0; i < data.length; i++) {
        if(data[i]['iso-a3'] === iso) {
          value = data[i]['value'];
          break;
        }
      }
    }

    return value;
  }

  function findUnconditionalValue(iso) {
    var value;

    // Capitalize first letter to match naming of cached data
    const approachMode = esign.cache.approachMode;
    const approachModeCapital = approachMode.charAt(0).toUpperCase() + approachMode.slice(1);

    const dataName = `warming${approachModeCapital}${esign.cache.responsibilitySince}Uncond`;
    const data = esign.cache[dataName];

    if(data.length != 0) {
      for (var i = 0; i < data.length; i++) {
        if(data[i]['iso-a3'] === iso) {
          value = data[i]['value'];
          break;
        }
      }
    }

    return value;
  }

  // Listeners
  var countryListItem = $('#country-list ul li'),
    tooltip, iso;

  countryListItem.click(function (event) {
    esign.cache.dataSelected = true;
    iso = this.getAttribute('data-iso');
    if(esign.cache.isoSelected !== iso) {
      for(var i = 0; i < esign.cache.map.series[0].data.length; i++) {
        if(esign.cache.map.series[0].data[i]['iso-a3'] === iso) {
          tooltip = esign.cache.map.series[0].data[i];
          tooltip.firePointEvent('click', event);
        }
      }
    }

    // TODO not needed? triggered function twice;
    if(esign.cache.countryGroupes.includes(iso)) {
      esign.cache.isoSelected = iso;
      for (var i = 0; i < esign.cache.countries.length; i++ ) {
        if(esign.cache.countries[i]['iso-a3'] === iso) esign.cache.countrySelected = esign.cache.countries[i]['country'];
      }
      console.log('countryListItem.click');
      // esign.openWarmingChart('', findConditionalValue(iso), findUnconditionalValue(iso));
    }
  });

  countryListItem.mouseenter(function () {
    if(!esign.cache.isMobile) {
      iso = this.getAttribute('data-iso');

      // if (esign.cache.warmingModeUpdated) {
      //   if (esign.cache.warmingSource === 1) {
      //     if (iso === 'EU27') {
      //       console.log('Iso 27');
      //       esign.handleWarmingDataSeries(seriesOptions, 'EU27');
      //     } else if (esign.cache.countriesEU27.indexOf(iso) > -1) {
      //       console.log('else');

      //       esign.handleWarmingDataSeries(seriesOptions);
      //     }
      //   }
      // } else {
      //   if (iso === 'EU28') {
      //     esign.handleWarmingDataSeries(seriesOptions, 'EU28');
      //     console.log('Iso 28');

      //   } else if (esign.cache.countriesEU28.indexOf(iso) > -1) {
      //     console.log('else 2');

      //     esign.handleWarmingDataSeries(seriesOptions);
      //   }
      // }

      // TODO: rework with country groups, so tooltip with data is visible for each group
      for(var i = 0; i < esign.cache.map.series[0].data.length; i++) {
        if(esign.cache.map.series[0].data[i]['iso-a3'] === iso) {
          tooltip = esign.cache.map.series[0].data[i];
          if (tooltip && tooltip.setState) {
            tooltip.setState('hover');
          }

          if(esign.cache.map.series[0].data[i].options.value != null && !esign.cache.modalDataView) {
            setTrafficLightCond(true, findConditionalValue(iso));
            setTrafficLightUncond(true, findUnconditionalValue(iso));
          }

          esign.cache.map.tooltip.update({
            positioner: function(val, val2, val3) {
              if (iso === 'EU27' || iso === 'EU28' || esign.cache.countryGroupes.includes(iso)) {
                return { x: this.chart.plotWidth/2, y: this.chart.plotHeight/3 };
              }
              return { x: val3.plotX, y: val3.plotY };
            }
          });

          esign.cache.map.tooltip.refresh(tooltip);
        } else {
          if(!esign.cache.modalDataView) {
            setTrafficLightCond(true, findConditionalValue(iso));
            setTrafficLightUncond(true, findUnconditionalValue(iso));
          }
        }
      }
    }
  });

  countryListItem.mouseleave(function () {
    if(!esign.cache.isMobile) {
      var iso = this.getAttribute('data-iso');

      // if (esign.cache.warmingModeUpdated) {
      //   if (esign.cache.countriesEU27.indexOf(iso) > -1 && esign.cache.warmingSource === 1) {
      //     esign.handleWarmingDataSeries(seriesOptions, 'EU27');
      //   }
      // } else {
      //   if (esign.cache.countriesEU28.indexOf(iso) > -1) {
      //     esign.handleWarmingDataSeries(seriesOptions, 'EU28');
      //   }
      // }

      if (esign.cache.map !== undefined) {
        if (tooltip && tooltip.setState) {
          tooltip.setState();
        }
        esign.cache.map.tooltip.hide();
      }

      if(!esign.cache.modalDataView) {
        setTrafficLightCond(false);
        setTrafficLightUncond(false);
      }
    }
  });

  countryListItem.removeClass('hide')

  if (esign.cache.warmingModeUpdated) {
    if (esign.cache.warmingSource === 0) {
      // Hide EU27 items
      countryListItem.each(function(index, element) {
        var $element = $(element);
        if (esign.cache.countriesEU27.indexOf($element.data('iso')) > -1) {
          $element.addClass('hide');
        }
      });
    }

    if (esign.cache.warmingSource === 2) {
      // Hide EU27 & all other than National EU Targets
      countryListItem.each(function(index, element) {
        if (esign.cache.countriesNationalEUTargets.indexOf($(element).data('iso')) < 0) {
          $(element).addClass('hide');
        }
      });
    }
  }
};

function setTrafficLightColor(state, value, containerEl) {
  const redLight = containerEl.querySelector('.red');
  const orangeLight = containerEl.querySelector('.orange');
  const greenLight = containerEl.querySelector('.green');

  greenLight.classList.remove("active");
  orangeLight.classList.remove("active");
  redLight.classList.remove("active");

  if(state) {
    if(value === 1) {
      greenLight.classList.add("active");
    } else if (value === 3) {
      orangeLight.classList.add("active");
    } else if (value === 5) {
      redLight.classList.add("active");
    }
  }
}

function setTrafficLightCond(state, value = null) {
  const trafficLightsEl = document.querySelector('.traffic-light--cond');
  setTrafficLightColor(state, value, trafficLightsEl);
}

function setTrafficLightUncond(state, value = null) {
  const trafficLightsEl = document.querySelector('.traffic-light--uncond');
  setTrafficLightColor(state, value, trafficLightsEl);
}

