/* Open warming chart view */
esign.openWarmingChart = function(color, valueCond, valueUncond) {
  var $graphCountryTitle = $('.graph-country__title');
  $graphCountryTitle.text(esign.cache.countrySelected);

  $('.modal-button-graph').trigger('click');
  esign.cache.modalDataView = true;

  setTrafficLightCond(true, valueCond);
  setTrafficLightUncond(true, valueUncond);

  // TODO make globally avaiable? Are defined twice now
  var $trigger1990 = $('.resp-1990');
  var $trigger1950 = $('.resp-1950');
  var $triggerModal1990 = $('.resp-modal-1990');
  var $triggerModal1950 = $('.resp-modal-1950');
  var $triggerGDP = $('.approach-gdp');
  var $triggerHDI = $('.approach-hdi');
  var $triggerModalGDP = $('.approach-modal-gdp');
  var $triggerModalHDI = $('.approach-modal-hdi');

  $triggerModal1990.click(function (e) {
    e.preventDefault();
    esign.cache.responsibilitySince = 1990;

    $trigger1950.removeClass('active');
    $triggerModal1950.removeClass('active');

    $trigger1990.addClass('active');
    $triggerModal1990.addClass('active');
    esign.loadWarmingChartData(color);
    esign.updateTrafficLights();
  });

  $triggerModal1950.click(function (e) {
    e.preventDefault();
    esign.cache.responsibilitySince = 1950;
    ///
    $trigger1990.removeClass('active');
    $triggerModal1990.removeClass('active');

    $trigger1950.addClass('active');
    $triggerModal1950.addClass('active');
    esign.loadWarmingChartData(color);
    esign.updateTrafficLights();
  });

  // HDI
  $triggerModalHDI.click(function (e) {
    e.preventDefault();
    esign.cache.approachMode = 'hdi';
    ///
    $triggerGDP.removeClass('active');
    $triggerModalGDP.removeClass('active');
    $triggerHDI.addClass('active');
    $triggerModalHDI.addClass('active');

    esign.loadWarmingChartData(color);
    esign.updateTrafficLights();
  });

  // GDP
  $triggerModalGDP.click(function (e) {
    e.preventDefault();
    esign.cache.approachMode = 'gdp';
    ///
    $triggerHDI.removeClass('active');
    $triggerModalHDI.removeClass('active');
    $triggerGDP.addClass('active');
    $triggerModalGDP.addClass('active');

    esign.loadWarmingChartData(color);
    esign.updateTrafficLights();
  });

  // TODO check if statement
  if (
    (esign.cache.warmingModeUpdated && !esign.cache.warmingConsistencyDataUpdated)
    || (!esign.cache.warmingModeUpdated && !esign.cache.warmingConsistencyData)
  ) {
    esign.loadWarmingChartData(color);
  } else {
    esign.initWarmingChartEvents(color);
  }
};

esign.updateTrafficLights = function() {
  function getCountryIso(value) {
    return value['iso-a3'] === esign.cache.isoSelected;
  }

  // Capitalize first letter to match naming of cached data
  const approachMode = esign.cache.approachMode;
  const approachModeCapital = approachMode.charAt(0).toUpperCase() + approachMode.slice(1);

  const dataName = `warming${approachModeCapital}${esign.cache.responsibilitySince}`;
  const dataNameUncond = `warming${approachModeCapital}${esign.cache.responsibilitySince}Uncond`;

  const valueCond = esign.cache[dataName].filter(getCountryIso);
  const valueUncond = esign.cache[dataNameUncond].filter(getCountryIso);

  setTrafficLightCond(true, valueCond[0].value);
  setTrafficLightUncond(true, valueUncond[0].value);
}

/* Load warming chart data */
esign.loadWarmingChartData = function (color) {
  var pathHistoricalCond = 'assets/data/new/historical_cond.json?v=' + esign.cache.version;
  var pathNdcCond = 'assets/data/new/ndc_cond.json?v=' + esign.cache.version;
  var pathNdcUncond = 'assets/data/new/ndc_uncond.json?v=' + esign.cache.version;

  // Approach data
  var pathHdi1990_1p5c = 'assets/data/new/hdi_1990_1p5c.json?v=' + esign.cache.version;
  var pathHdi1990_2c = 'assets/data/new/hdi_1990_2c.json?v=' + esign.cache.version;
  var pathGdp1990_1p5c = 'assets/data/new/gdp_1990_1p5c.json?v=' + esign.cache.version;
  var pathGdp1990_2c = 'assets/data/new/gdp_1990_2c.json?v=' + esign.cache.version;

  var pathHdi1950_1p5c = 'assets/data/new/hdi_1950_1p5c.json?v=' + esign.cache.version;
  var pathHdi1950_2c = 'assets/data/new/hdi_1950_2c.json?v=' + esign.cache.version;
  var pathGdp1950_1p5c = 'assets/data/new/gdp_1950_1p5c.json?v=' + esign.cache.version;
  var pathGdp1950_2c = 'assets/data/new/gdp_1950_2c.json?v=' + esign.cache.version;

  var pathGroups = 'assets/data/new/groups.json?v=' + esign.cache.version;
  var pathNatTargets = 'assets/data/new/national-targets.json?v=' + esign.cache.version;
  var pathIndicators = 'assets/data/new/indicators.json?v=' + esign.cache.version;
  var pathIndicatorsHdi = 'assets/data/new/indicators-hdi.json?v=' + esign.cache.version;

  $.when(
    $.getJSON(pathHistoricalCond, function(data) {
      esign.cache.historicalCond = data;
    }),
    $.getJSON(pathNdcCond, function(data) {
      esign.cache.ndcCond = data;
    }),
    $.getJSON(pathNdcUncond, function(data) {
      esign.cache.ndcUncond = data;
    }),
    $.getJSON(pathHdi1990_1p5c, function(data) {
      esign.cache.hdi1990_1p5c = data;
    }),
    $.getJSON(pathHdi1990_2c, function(data) {
      esign.cache.hdi1990_2c = data;
    }),
    $.getJSON(pathGdp1990_1p5c, function(data) {
      esign.cache.gdp1990_1p5c = data;
    }),
    $.getJSON(pathGdp1990_2c, function(data) {
      esign.cache.gdp1990_2c = data;
    }),
    $.getJSON(pathHdi1950_1p5c, function(data) {
      esign.cache.hdi1950_1p5c = data;
    }),
    $.getJSON(pathHdi1950_2c, function(data) {
      esign.cache.hdi1950_2c = data;
    }),
    $.getJSON(pathGdp1950_1p5c, function(data) {
      esign.cache.gdp1950_1p5c = data;
    }),
    $.getJSON(pathGdp1950_2c, function(data) {
      esign.cache.gdp1950_2c = data;
    }),
    $.getJSON(pathGroups, function(data) {
      esign.cache.groups = data;
    }),
    $.getJSON(pathIndicators, function(data) {
      esign.cache.indicators = data;
    }),
    $.getJSON(pathIndicatorsHdi, function(data) {
      esign.cache.indicatorsHdi = data;
    }),
    $.getJSON(pathNatTargets, function(data) {
      esign.cache.natTargets = data;
    })
  ).done(function() {
    esign.initWarmingChartEvents(color);
  });
};

/* Init warming chart events */
esign.initWarmingChartEvents = function (color) {
  // TODO rename
  $('.controls--w3 .rel-year').click(function (e) {
    e.preventDefault();

    esign.cache.isAbsolute = false;

    esign.cache.$relativeTrigger3.addClass('active');
    esign.cache.$absoluteTrigger3.removeClass('active');


    var year = this.text.replace('rel-', '');
    esign.cache.$relativeTrigger3.html('Relative to ' + year + ' <span class="caret"></span><span class="sr-only">Toggle Dropdown</span>');

    esign.createCountryDataView(year, color);

  });

  esign.cache.$absoluteTrigger3.click(function (e) {
    e.preventDefault();

    if (!esign.cache.isAbsolute) {
      esign.cache.isAbsolute = true;
      esign.cache.$relativeTrigger3.removeClass('active');
      esign.cache.$absoluteTrigger3.addClass('active');
      esign.createCountryDataView(2021, color);
    }
  });


  // TODO relocate?
  function getCountryIso(value) {
    return value['iso-a3'] === esign.cache.isoSelected;
  }
  const target = esign.cache.natTargets.filter(getCountryIso);
  if(target.length != 0) {
    esign.showCountryTargetNote(target[0]);
  }

  esign.createCountryDataView(2021, color);
  esign.createCountryIndicators();
}

esign.createCountryIndicators = function() {
  function getCountryIso(value) {
    return value['iso-a3'] === esign.cache.isoSelected;
  }
  const data = esign.cache.indicators.filter(getCountryIso);
  const dataHdi = esign.cache.indicatorsHdi.filter(getCountryIso);

  const currCapita = document.querySelector('.js-country-capita');
  currCapita.innerHTML = data[0]['current-capita'];

  const currGlobalEmission = document.querySelector('.js-country-global-emissions');
  currGlobalEmission.innerHTML = data[0]['global-emissions'];

  const currCountryHdi = document.querySelector('.js-country-hdi');
  console.log(dataHdi.length);
  if(dataHdi.length > 0) {
    currCountryHdi.innerHTML = dataHdi[0]['hdi'];
  } else {
    currCountryHdi.innerHTML = '/';
  }
}

esign.showCountryTargetNote = function(data) {
  const container = document.querySelector('.js-target-notes');
  container.innerHTML = `
  <div class="mb60 mt40">
    <h3>Information on the national emissions target:</h3>
    <p>${data.note}</p>
  </div>`;
}

esign.loadCountryGroups = function (selectedIso) {
  const overview = document.querySelector('.js-countries-group');
  overview.classList.add('d-none');

  esign.cache.groups.forEach(group => {
    if(group.name === selectedIso) {
      esign.showCountriesFromCountryGroup(group.countries, overview);
    }
  })
}

esign.showCountriesFromCountryGroup = function (countries, overview) {
  overview.classList.remove('d-none');
  const grid = document.querySelector('.js-countries-grid');
  grid.innerHTML = ``;

  countries.forEach(country => {
    if(country.value === '1') {
      const countryData = esign.cache.countries.filter((el) => el["iso-a3"] === country['iso-3']);

      console.log(countryData);

      if(countryData[0]) {
        const countryEl = document.createElement("div");
        countryEl.classList.add('countries-grid__item');
        countryEl.innerHTML = `
          <div class="modal-traffic-lights">
            <div>
              <div class="traffic-light traffic-light-cond" data-iso="${country['iso-3']}">
                <span class="red"></span>
                <span class="orange"></span>
                <span class="green"></span>
              </div>
              <span class="traffic-light__subtitle">cond.</span>
            </div>

            <div>
              <div class="traffic-light traffic-light-uncond" data-iso="${country['iso-3']}">
                <span class="red"></span>
                <span class="orange"></span>
                <span class="green"></span>
              </div>
              <span class="traffic-light__subtitle">uncond.</span>
            </div>
          </div>
          <p>${countryData[0].country}</p>`;
        grid.appendChild(countryEl);

        // TODO merge functions esign.updateTrafficLights & esign.updateTrafficLightsCountryGroups???
        esign.updateTrafficLightsCountryGroups(country);
      }
    }
  })
}

esign.updateTrafficLightsCountryGroups = function (country) {
  function getCountryIso(value) {
    return value['iso-a3'] === country['iso-3'];
  }

  // Capitalize first letter to match naming of cached data
  const approachMode = esign.cache.approachMode;
  const approachModeCapital = approachMode.charAt(0).toUpperCase() + approachMode.slice(1);

  const dataName = `warming${approachModeCapital}${esign.cache.responsibilitySince}`;
  const dataNameUncond = `warming${approachModeCapital}${esign.cache.responsibilitySince}Uncond`;

  const valueCond = esign.cache[dataName].filter(getCountryIso);
  const valueUncond = esign.cache[dataNameUncond].filter(getCountryIso);

  const trafficLightsCondEl = document.querySelector(`.traffic-light-cond[data-iso="${country['iso-3']}"]`);
  setTrafficLightColor(true, valueCond[0].value, trafficLightsCondEl);

  const trafficLightsUncondEl = document.querySelector(`.traffic-light-uncond[data-iso="${country['iso-3']}"]`);
  setTrafficLightColor(true, valueUncond[0].value, trafficLightsUncondEl);
}

esign.createCountryDataView = function (year, color) {
  const yearMin = 1990,
    yearDivide = 2021,
    yearMax = 2050,
    unitK = 'ktCO2eq',
    unitM = 'MtCO2eq',
    unitG = 'GtCO2eq';

  let unit = unitK,
    divider = 1;

  // TODO: Finish feature to show al countries of a group with their traffic lights
  esign.loadCountryGroups(esign.cache.isoSelected);

  function getCountryIso(value) {
    return value['iso-a3'] === esign.cache.isoSelected;
  }

  // new data (cond)
  const dataHistory = esign.cache.historicalCond.filter(getCountryIso);
  const dataNdc = esign.cache.ndcCond.filter(getCountryIso);
  const dataNdcUncond = esign.cache.ndcUncond.filter(getCountryIso);
  const dataHdi1990_1p5c = esign.cache.hdi1990_1p5c.filter(getCountryIso);
  const dataHdi1990_2c = esign.cache.hdi1990_2c.filter(getCountryIso);
  const dataGdp1990_1p5c = esign.cache.gdp1990_1p5c.filter(getCountryIso);
  const dataGdp1990_2c = esign.cache.gdp1990_2c.filter(getCountryIso);

  const dataHdi1950_1p5c = esign.cache.hdi1950_1p5c.filter(getCountryIso);
  const dataHdi1950_2c = esign.cache.hdi1950_2c.filter(getCountryIso);
  const dataGdp1950_1p5c = esign.cache.gdp1950_1p5c.filter(getCountryIso);
  const dataGdp1950_2c = esign.cache.gdp1950_2c.filter(getCountryIso);

  let trajectoryHistory = [], trajectoryNDC = [], trajectoryNdcCondRange = [], trajectoryNdcUncond = [], trajectoryNdcUncondRange = [], trajectoryHdi1990_1p5c = [], trajectoryHdi1990_2c = [], trajectoryGdp1990_1p5c = [], trajectoryGdp1990_2c = [], trajectoryHdi1950_1p5c = [], trajectoryHdi1950_2c = [], trajectoryGdp1950_1p5c = [], trajectoryGdp1950_2c = [];
  let maxValue = 0;

  if(esign.cache.isAbsolute) {
    for(var i = yearMin; i <= yearDivide; i++) {
      // Mega to kilo
      if(dataHistory.length != 0) {
        trajectoryHistory.push([i, (dataHistory[0][i] * 1000)]);
      }
    }
    // Giga to kilo
    for(var i = yearDivide; i <= yearMax; i++) {
      trajectoryHdi1990_1p5c.push([i, (dataHdi1990_1p5c[0][i] * 1000000)]);
      trajectoryHdi1990_2c.push([i, (dataHdi1990_2c[0][i] * 1000000)]);
      trajectoryGdp1990_1p5c.push([i, (dataGdp1990_1p5c[0][i] * 1000000)]);
      trajectoryGdp1990_2c.push([i, (dataGdp1990_2c[0][i] * 1000000)]);

      trajectoryHdi1950_1p5c.push([i, (dataHdi1950_1p5c[0][i] * 1000000)]);
      trajectoryHdi1950_2c.push([i, (dataHdi1950_2c[0][i] * 1000000)]);
      trajectoryGdp1950_1p5c.push([i, (dataGdp1950_1p5c[0][i] * 1000000)]);
      trajectoryGdp1950_2c.push([i, (dataGdp1950_2c[0][i] * 1000000)]);
      max([dataHdi1990_1p5c[0][i] * 1000000, dataHdi1990_2c[0][i] * 1000000, dataGdp1990_1p5c[0][i] * 1000000, dataGdp1990_2c[0][i] * 1000000, dataHdi1950_1p5c[0][i] * 1000000, dataHdi1950_2c[0][i] * 1000000, dataGdp1950_1p5c[0][i] * 1000000, dataGdp1950_2c[0][i] * 1000000]);
    }

    // Mega to kilo
    if(dataNdc.length != 0) {
      trajectoryNDC.push([2030, (dataNdc[0].avg * 1000)]);
      trajectoryNdcCondRange.push([dataNdc[0].low * 1000, dataNdc[0].high * 1000]);
    }

    if(dataNdcUncond.length != 0) {
      trajectoryNdcUncond.push([2030, (dataNdcUncond[0].avg * 1000)]);
      trajectoryNdcUncondRange.push([dataNdcUncond[0].low * 1000, dataNdcUncond[0].high * 1000]);
    }
  } else {
    for(var i = yearMin; i <= yearDivide; i++) {
      if(dataHistory.length != 0) {
        trajectoryHistory.push([i, (dataHistory[0][i]/dataHistory[0][year])*100]);
      }
    }

    if(dataNdc.length != 0) {
      trajectoryNDC.push([2030, (dataNdc[0].avg/dataHistory[0][year])*100]);
      trajectoryNdcCondRange.push([(dataNdc[0].low/dataHistory[0][year])*100, (dataNdc[0].high/dataHistory[0][year])*100]);
    }

    if(dataNdcUncond.length != 0) {
      trajectoryNdcUncond.push([2030, (dataNdcUncond[0].avg/dataHistory[0][year])*100]);
      trajectoryNdcUncondRange.push([(dataNdcUncond[0].low/dataHistory[0][year])*100, (dataNdcUncond[0].high/dataHistory[0][year])*100]);
    }

    for(var i = yearMin; i <= yearMax; i++) {
      trajectoryHdi1990_1p5c.push([i, ((dataHdi1990_1p5c[0][i] * 1000)/dataHistory[0][year])*100]);
      trajectoryHdi1990_2c.push([i, ((dataHdi1990_2c[0][i] * 1000)/dataHistory[0][year])*100]);
      trajectoryGdp1990_1p5c.push([i, ((dataGdp1990_1p5c[0][i] * 1000)/dataHistory[0][year])*100]);
      trajectoryGdp1990_2c.push([i, ((dataGdp1990_2c[0][i] * 1000)/dataHistory[0][year])*100]);

      trajectoryHdi1950_1p5c.push([i, ((dataHdi1950_1p5c[0][i] * 1000)/dataHistory[0][year])*100]);
      trajectoryHdi1950_2c.push([i, ((dataHdi1950_2c[0][i] * 1000)/dataHistory[0][year])*100]);
      trajectoryGdp1950_1p5c.push([i, ((dataGdp1950_1p5c[0][i] * 1000)/dataHistory[0][year])*100]);
      trajectoryGdp1950_2c.push([i, ((dataGdp1950_2c[0][i] * 1000)/dataHistory[0][year])*100]);
    }
  }

  function max(arr) {
    var m = Math.max.apply(null, arr);
    if(m > maxValue) maxValue = m;
  }

  if(maxValue > 999) {
    unit = unitM;
    divider = 1000;
  }
  if(maxValue > 999999) {
    unit = unitG;
    divider = 1000000;
  }

  let yAxis = {};

  if(esign.cache.isAbsolute) {
    // Absolute options
    yAxis = {
      title: {
        text: 'GHG emissions in ' + unit,
        fontSize: '16px',
      },
      plotLines: [{
        value: 0,
        color: 'black',
        width: 2
      }],
      labels: {
        formatter: function () {
          if(unit === unitM) {
            return this.value / divider;
          } else if (unit === unitG) {
            return this.value / divider;
          }
          return this.value;
        }
      }
    };
  } else {
    // Relative options
    yAxis = {
      title: {
        text: 'GHG emissions allowances in % of ' + year + ' levels'
      },
      plotLines: [{
        value: 100,
        color: 'black',
        dashStyle: 'shortdash',
        width: 2
      }, {
        value: 0,
        color: 'black',
        width: 2
      }],
      labels: {
        formatter: function () {
          return this.value;
        }
      }
    };
  }

  var xAxis = {
    title: {
      text: 'Time (years)'
    },
    plotLines: [{
      value: 2021,
      color: '#FDBE13',
      width: 1
    }],
  }

  var chartOptions = {
    chart: {
      renderTo: 'chart3',
      style: {
        fontFamily: 'Avenir',
        fontSize: '1em',
      },
      backgroundColor: '#ffffff'
    },
    title: {
     text: esign.cache.countrySelected,
      useHTML: true
    },
    subtitle: {
      text: '',
      style: {
        color: '#B4B4B4',
        fontSize: '15px'
      },
      useHTML: true
    },
    xAxis: xAxis,
    yAxis: yAxis,
    tooltip: {
      crosshairs: true,
      shared: true,
      useHtml: true,
      borderWidth: 0,
      hideDelay: 30
    },
    legend: {
      title: {
        text: 'Legend',
        style: {
          fontWeight: 'normal'
        }
      },
      enabled: true,
      layout: 'vertical',
      floating: false,
      align: 'center left',
      itemStyle: {
        fontWeight: 'normal',
        fontSize: '12px'
      },
      itemMarginBottom: 1,
    },
    exporting: {
      enabled: false,
      filename: esign.cache.countrySelected + " - Data view",
      fallbackToExportServer: true,
      chartOptions:{
        credits: {
          enabled: true,
          href:"#",
          position: {
            align:"right",
            verticalAlign:"bottom",
            x: -10,
            y: -5,
          },
          style: {"cursor": "default", "font-size":"6pt"},
          text:"From trafficlightclimate.org, Commitioned by the CVF/V20"
        }
      }
    },
    credits: false,
    series: [
      {
      name: 'Historical emissions',
      data: trajectoryHistory,
      zIndex: 1,
      color: '#000000',
      lineWidth: 3.5,
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2021,
        color: '#000000'
      }]
    },
    {
      id: 'ndc-cond',
      name: 'NDC conditional',
      data: trajectoryNDC,
      type: 'scatter',
      zIndex: 999,
      marker: {
        lineWidth: 0,
        radius: 8,
        fillColor: '#91EF8F',
        symbol: 'circle',
        enabled: !!trajectoryNDC
      },
      showInLegend: true
    },
    {
      name: 'NDC conditional',
      data: trajectoryNdcCondRange,
      type: 'errorbar',
      zIndex: 998,
      showInLegend: false,
      linkedTo: 'ndc-cond',
      pointStart: trajectoryNDC[0][0],
      stemWidth: 2,
      pointWidth: 3,
      color: '#9A9A9A',
      animation: false,
    },
    {
      id: 'ndc-uncond',
      name: 'NDC unconditional',
      data: trajectoryNdcUncond,
      type: 'scatter',
      zIndex: 999,
      marker: {
        lineWidth: 0,
        radius: 8,
        fillColor: '#036400',
        symbol: 'circle',
        enabled: !!trajectoryNdcUncond
      },
      showInLegend: true
    },
    {
      name: 'NDC unconditional',
      data: trajectoryNdcUncondRange,
      type: 'errorbar',
      zIndex: 998,
      showInLegend: false,
      linkedTo: 'ndc-uncond',
      pointStart: trajectoryNdcUncond[0][0],
      stemWidth: 2,
      pointWidth: 3,
      color: '#9A9A9A',
      animation: false,
    },
  ]
  };

  // Create country data chart, with defined chartOptions above;
  const chart = new Highcharts.Chart(chartOptions);

  const target = esign.cache.natTargets.filter(getCountryIso);
  if(target.length != 0) {
    const targetHistoryValue = trajectoryHistory.filter((el) => el[0] === target[0].year);
    const startValue = targetHistoryValue[0][1];
    const targetValue = startValue * ((100 - target[0].percentage) / 100);

    if(target[0]["iso-a3"] === 'PRT') {
      const targetValueA = startValue * ((100 - 45) / 100);
      const targetValueB = startValue * ((100 - 55) / 100);

      chart.addSeries({
        name: `National target range, (${target[0].label})`,
        data: [[targetValueA, targetValueB]],
        type: 'errorbar',
        zIndex: 998,
        showInLegend: true,
        pointStart: 2030,
        stemWidth: 2,
        pointWidth: 3,
        color: '#0000FF',
        animation: false,
      });
    } else {
      chart.addSeries({
        name: `National target, (${target[0].label})`,
        data: [[2030, targetValue]],
        type: 'scatter',
        zIndex: 999,
        marker: {
          symbol:
            "url(data:image/png;base64,)"
          //"url(data:image/png;base64,)"
          ,
          lineWidth: 2,
          radius: 8,
          lineColor: 'black',
          fillColor: 'black'
        },
        showInLegend: true
      });
    }
  }

  if (esign.cache.approachMode === 'hdi') {
    chart.addSeries({
      name: `Approach HDI (1.5C, ${esign.cache.responsibilitySince})`,
      data: esign.cache.responsibilitySince === 1990 ? trajectoryHdi1990_1p5c : trajectoryHdi1950_1p5c,
      zIndex: 10,
      color: '#780179',
      lineWidth: 3.5,
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2021,
        color: 'transparent'
      }]
    });
    chart.addSeries({
      name: `Approach HDI (2C, ${esign.cache.responsibilitySince})`,
      data: esign.cache.responsibilitySince === 1990 ? trajectoryHdi1990_2c : trajectoryHdi1950_2c,
      zIndex: 10,
      color: '#780179',
      dashStyle: 'ShortDash',
      lineWidth: 3.5,
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2021,
        color: 'transparent'
      }]
    });
  }

  if (esign.cache.approachMode === 'gdp') {
    chart.addSeries({
      name: `Approach GDP (1.5C, ${esign.cache.responsibilitySince})`,
      data: esign.cache.responsibilitySince === 1990 ? trajectoryGdp1990_1p5c : trajectoryGdp1950_1p5c,
      zIndex: 10,
      color: '#FE5336',
      lineWidth: 3.5,
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2021,
        color: 'transparent'
      }]
    });
    chart.addSeries({
      name: `Approach GDP (2C, ${esign.cache.responsibilitySince})`,
      data: esign.cache.responsibilitySince === 1990 ? trajectoryGdp1990_2c : trajectoryGdp1950_2c,
      zIndex: 10,
      color: '#FE5336',
      dashStyle: 'ShortDash',
      lineWidth: 3.5,
      marker: {
        lineWidth: 0,
        radius: 0,
        symbol: 'circle',
        lineColor: 'transparent'
      },
      zoneAxis: 'x',
      zones: [{
        value: 2021,
        color: 'transparent'
      }]
    });
  }

  // Create tooltip
  chart.tooltip.options.formatter = function() {
    var points = this.points || Highcharts.splat(this),
      txt = '';

    txt += '<span class="tooltip-year">' + this.x + '</span><br>';

    if(this.x <= yearDivide) {
      Highcharts.each(points, function(p, i){
        var name = p.series.name;

        txt += name + ': ' + (p.y/divider).toPrecision(3) + '<br>';
      });
    } else {
      console.log('else');
      Highcharts.each(points, function(p, i){
        var name = p.series.name;

        txt += name + ': ' + (p.y/divider).toPrecision(3) + '<br>';
      });
    }
    return txt;
  };

  // Exports
  esign.resetExportListeners3();

  $('#export-chart-print3').click(function () {
    chart.print();
  });
  $('#export-chart-png3').click(function () {
    chart.exportChartLocal({
      type: 'image/png'
    });
  });
  $('#export-chart-jpg3').click(function () {
    chart.exportChartLocal({
      type: 'image/jpeg'
    });
  });
  $('#export-chart-pdf3').click(function () {
    chart.exportChartLocal({
      type: 'application/pdf'
    });
  });
  $('#export-chart-csv3').click(function () {
    chart.downloadCSV();
  });
  $('#export-chart-xls3').click(function () {
    chart.downloadXLS();
  });
  $('#export-chart-svg3').click(function () {
    chart.exportChartLocal({
      type: 'image/svg+xml'
    });
  });
};

esign.round = function (value, decimals) {
  return Number(Math.round(value+'e'+decimals)+'e-'+decimals);
};

esign.decodeHTML = function (html) {
  var txt = document.createElement('textarea');
  txt.innerHTML = html;
  return txt.value;
};

esign.getValueByIso = function(arr, iso) {
  function getCountryIso(value) {
    return value['iso-a3'] === iso;
  }
  return arr.filter(getCountryIso)[0];
}

